import { BrowserRouter, Routes, Route } from "react-router-dom";
import App from "./App";
import {Credits} from "./components/credits";
import {useState} from "react";

function Main() {

  let [test, setTest] = useState("bonjour")
  return(
    <BrowserRouter>
      <Routes>
        <Route path="/" element={<App props={{test}}/>}/>
        <Route path="/credits" element={<Credits props={{test, setTest}}/>}/>
      </Routes>
    </BrowserRouter>
  )
}
export {Main}