import React, {useEffect, useState} from "react";
import * as utils from "../utils/utils";
import * as temp_data from "../utils/temporary_data";
import axios from "axios";
import * as calls from "../calls";

/*Component for the selection of the current patient
* -patientList : list of all patients
* -currentPatient : current patient state variable
* -setCurrentPatient: current patient set function
* */
function UserBox ({props}) {

  let {onNewScanResult, loginErrorCode, isLogin} = props
  let [isWaitingLogin, setIsWaitingLogin] = useState(false)

  function debug_allow_user() {
    setIsWaitingLogin(true)
    onNewScanResult("theo.stassen@hepl.be_Test123.")
  }



  /*Return the patient box elements*/
  return (
    <div className="container p-2 mt-2">
      {isWaitingLogin? <div className="d-flex justify-content-center m-2">
        <div className="spinner-border" role="status">
          <span className="sr-only">Loading...</span>
        </div>
      </div> : null
      }
      <div className={"iq-card bg-white col-sm-12 mx-auto mb-0 p-2 text-center shadow-sm border justify-content-center"}>
        <div className="card-body col-sm-6 mx-auto">
          <h3 className="card-title text-dark"> Identification de l'utilisateur </h3>

          {loginErrorCode ?
            <div className={"iq-card mx-auto text-center p-2 shadow-sm border justify-content-center mt-2"}>
              <h6 className="card-text text-danger m-0 p-0"> <div data-icon="&#xe063;" className="icon text-danger"> Aucun utilisateur n'est trouvé, problème de connexion ("{loginErrorCode}") </div></h6>
            </div>
          : null}
          <h4 className="card-title text-secondary mt-4 mb-0">Scannez le QR code correspondant</h4>
          <h4 className="card-title text-secondary"><div data-icon="k" className="icon"></div></h4>

          <button className={"btn btn-outline-primary btn-round m-2"} onClick={() =>debug_allow_user()}>Passer Qr Code</button>
        </div>
      </div>
    </div>
  )
}

export {UserBox}